export function ajaxCall(url, action, nonce, dataName, data) {

        let formData = new FormData();
        formData.append('action', action);
        formData.append('security', nonce);
        formData.append(dataName, data);

    return fetch(url, {
        method: 'POST',
        body: formData,
        credentials: 'same-origin'
    });
}