<template>
  <div id="udesly-query-wrapper">
    <div class="query-menu">
      <slot></slot>
    </div>
    <div class="posts-preview">
      <div v-if="posts.length">
        <p>Found: {{ posts.length }}</p>
        <div class="posts-wrapper">
          <div class="post-preview" v-for="post in posts" v-bind:key="post.ID">
            <a target="blank" :href="post.guid">{{ post.post_title || post.name }}</a>
          </div>
        </div>
      </div>
      <div v-else>
        <div class="posts-preview">
          <p>No Posts Found</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import Vuex from "vuex";
import MaterialButton from "./components/MaterialButton";
import { ajaxCall } from "./utils/ajax";
import { setTimeout } from "timers";
import { isArray } from 'util';

Vue.use(Vuex);

export const store = new Vuex.Store({
  state: {
    ...udeslyQueryConfig,
    currentMenuPath: "",
    previewPosts: {
      disabled: false,
      status: "idle"
    },
    saveQuery: {
      disabled: false,
      status: "idle"
    },
    previewTaxonomies: {
      disabled: false,
      status: "idle"
    },
    saveTermQuery: {
      disabled: false,
      status: "idle"
    }
  },
  mutations: {
    CHANGE_MENU_PATH(state, payload) {
      state.currentMenuPath = payload;
    },
    CHANGE_OPTION(state, payload) {
      state.config[payload.name] = payload.value;
    },
    CHANGE_ACTIONS_STATUS(state, payload) {
      const oldState = state[payload.action];
      state[payload.action] = { ...oldState, ...payload.value };
    },
    CHANGE_STATE(state, payload) {
      state[payload.key] = payload.value;
    }
  },
  actions: {
    changePath({ commit }, path) {
      commit("CHANGE_MENU_PATH", path);
    },
    changeOption({ commit }, op) {
      commit("CHANGE_OPTION", op);
    },
    previewPosts({ commit, state }) {
      commit("CHANGE_ACTIONS_STATUS", {
        action: "previewPosts",
        value: { disabled: true, status: "loading" }
      });
      ajaxCall(
        state.ajaxurl,
        "udesly_preview_posts",
        state.nonce,
        "args",
        JSON.stringify(state.config)
      )
        .then(res => {
          if (res.ok) {
            commit("CHANGE_ACTIONS_STATUS", {
              action: "previewPosts",
              value: { disabled: true, status: "success" }
            });
            res.json().then(data => {
              if (data.data) {
                commit("CHANGE_STATE", { key: "posts", value: data.data });
              } else {
                commit("CHANGE_STATE", { key: "posts", value: [] });
              }
            });
          } else {
            commit("CHANGE_ACTIONS_STATUS", {
              action: "previewPosts",
              value: { disabled: true, status: "failed" }
            });
          }
        })
        .finally(() => {
          setTimeout(function() {
            commit("CHANGE_ACTIONS_STATUS", {
              action: "previewPosts",
              value: { disabled: false, status: "idle" }
            });
          }, 1500);
        });
    },
    saveQuery({ commit, state }) {
      commit("CHANGE_ACTIONS_STATUS", {
        action: "saveQuery",
        value: { disabled: true, status: "loading" }
      });
      window.addEventListener("beforeunload.edit-post", function(e) {
        e.preventDefault();
      });
      ajaxCall(
        state.ajaxurl,
        "udesly_save_post_query",
        state.nonce,
        "args",
        JSON.stringify({ config: state.config, post_id: state.post_id })
      )
        .then(res => {
          if (res.ok) {
            commit("CHANGE_ACTIONS_STATUS", {
              action: "saveQuery",
              value: { disabled: true, status: "success" }
            });
          } else {
            commit("CHANGE_ACTIONS_STATUS", {
              action: "saveQuery",
              value: { disabled: true, status: "failed" }
            });
          }
        })
        .finally(() => {
          setTimeout(function() {
            commit("CHANGE_ACTIONS_STATUS", {
              action: "saveQuery",
              value: { disabled: false, status: "idle" }
            });
          }, 1500);
        });
    },
    saveTermQuery({ commit, state }) {
      commit("CHANGE_ACTIONS_STATUS", {
        action: "saveTermQuery",
        value: { disabled: true, status: "loading" }
      });
      window.addEventListener("beforeunload.edit-post", function(e) {
        e.preventDefault();
      });
      ajaxCall(
        state.ajaxurl,
        "udesly_save_term_query",
        state.nonce,
        "args",
        JSON.stringify({ config: state.config, post_id: state.post_id })
      )
        .then(res => {
          if (res.ok) {
            commit("CHANGE_ACTIONS_STATUS", {
              action: "saveTermQuery",
              value: { disabled: true, status: "success" }
            });
          } else {
            commit("CHANGE_ACTIONS_STATUS", {
              action: "saveTermQuery",
              value: { disabled: true, status: "failed" }
            });
          }
        })
        .finally(() => {
          setTimeout(function() {
            commit("CHANGE_ACTIONS_STATUS", {
              action: "saveTermQuery",
              value: { disabled: false, status: "idle" }
            });
          }, 1500);
        });
    },
    previewTaxonomies({ commit, state }) {
      commit("CHANGE_ACTIONS_STATUS", {
        action: "previewTaxonomies",
        value: { disabled: true, status: "loading" }
      });

      ajaxCall(
        state.ajaxurl,
        "udesly_preview_taxonomies",
        state.nonce,
        "args",
        JSON.stringify(state.config)
      )
        .then(res => {
          if (res.ok) {
            commit("CHANGE_ACTIONS_STATUS", {
              action: "previewTaxonomies",
              value: { disabled: true, status: "success" }
            });
            res.json().then(data => {
              
              if (data.data) {
                let res = data.data;
                if (!isArray(data.data)) {
                  res = [];
                  for (let i in data.data) {
                    res.push(data.data[i]);
                  }
                }
                commit("CHANGE_STATE", { key: "posts", value: res });
              } else {
                commit("CHANGE_STATE", { key: "posts", value: [] });
              }
            });
          } else {
            commit("CHANGE_ACTIONS_STATUS", {
              action: "previewTaxonomies",
              value: { disabled: true, status: "failed" }
            });
          }
        })
        .finally(() => {
          setTimeout(function() {
            commit("CHANGE_ACTIONS_STATUS", {
              action: "previewTaxonomies",
              value: { disabled: false, status: "idle" }
            });
          }, 1500);
        });
    }
  },
  getters: {
    optionValue: state => name => {
      return state.config[name] || "";
    },
    getActionStatus: state => action => {
      return state[action].status;
    },
    getActionDisabled: state => action => {
      return state[action].disabled;
    },
    getTaxonomies: state => post_type => {
      return state["taxonomies"][post_type];
    }
  }
});

export default {
  name: "app",
  store,
  data: () => ({}),
  mounted: function() {},
  computed: {
    currentMenuPath() {
      return this.$store.state.currentMenuPath;
    },
    posts() {
      return this.$store.state.posts || [];
    },
    taxonomies() {
      return this.$store.state.taxonomies;
    },
    post_type() {
      return this.$store.getters.optionValue("post_type");
    }
  },
  methods: {},
  components: {},
  watch: {
    post_type(newValue, oldValue) {
      if ((newValue, oldValue)) {
        this.$store.dispatch("changeOption", { name: "terms", value: [] });
        this.$store.dispatch("changeOption", { name: "taxonomy", value: "" });
      }
    }
  }
};
</script>

