<template>
  <div class="multiple-select-wrapper">
    <span>
      <slot></slot>
    </span>
    <Multiselect
      v-model="value"
      :multiple="true"
      :options="optionArray"
      :custom-label="customLabel"
    />
  </div>
</template>

<script>
import "vue-multiselect/dist/vue-multiselect.min.css";
import Multiselect from "vue-multiselect";
import { ajaxCall } from "../utils/ajax";

export default {
  name: "udesly-multi-simple",
  props: ["options", "search", "name"],
  data: () => ({
    optionArray: [],
    optionsRaw: {}
  }),
  components: {
    Multiselect
  },
  computed: {
    value: {
      get() {
        return this.$store.getters.optionValue(this.name);
      },
      set(value) {
        this.$store.dispatch("changeOption", { name: this.name, value });
      }
    }
  },
  mounted: function() {
    this.optionsRaw = JSON.parse(this.options) || {};
    this.optionArray = [];
    for (let i in this.optionsRaw) {
      this.optionArray.push(i);
    }
  },
  methods: {
    customLabel(value) {
      return this.optionsRaw[value];
    }
  }
};
</script>

<style lang="scss" scoped>
.multiple-select-wrapper {
  display: flex;
  align-items: center;
  span {
    margin-right: 8px;
  }
  margin-bottom: 8px;
}
</style>
