<template>
  <div>
    
      <div class="query-list-item" v-if="isMenu" @click="changePath">
        <slot></slot>
      </div>
    
   <transition name="fade">
        <div class="content" v-if="isCurrent">
          <slot name="content"></slot>
        </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: "ListOption",
  props: ["name"],
  methods: {
    changePath() {
      this.$store.dispatch("changePath", this.name);
    }
  },
  computed: {
    isCurrent() {
      return this.$store.state.currentMenuPath == this.name;
    },
    isMenu() {
      return this.$store.state.currentMenuPath == "";
    }
  }
};
</script>

<style>
.query-list-item {
  background-color: #f7f7f7;
  padding: 8px 12px;
  border-radius: 4px;
  cursor: pointer;
  margin: 8px 0px;
}
.fade-enter-active {
  transition: opacity 0.5s ease-in;
}

.fade-leave-active {
    opacity: 0;
}

.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

</style>
