<template>
  <div class="multiple-select-wrapper">
    <span>
      <slot></slot>
    </span>
    <Multiselect
      v-model="value"
      :multiple="true"
      :searchable="true"
      :loading="isLoading"
      @search-change="asyncFind"
      :options="optionArray"
      :custom-label="customLabel"
    />
  </div>
</template>

<script>
import "vue-multiselect/dist/vue-multiselect.min.css";
import Multiselect from "vue-multiselect";
import { ajaxCall } from "../utils/ajax";

export default {
  name: "udesly-multi",
  props: ["options", "search", "name"],
  data: () => ({
    optionArray: [],
    optionsRaw: {},
    isLoading: false
  }),
  components: {
    Multiselect
  },
  computed: {
    value: {
      get() {
        return this.$store.getters.optionValue(this.name);
      },
      set(value) {
        this.$store.dispatch("changeOption", { name: this.name, value });
      }
    },
    taxonomy: function() {
      return this.$store.getters.optionValue("taxonomy");
    }
  },
  watch: {
    taxonomy(newValue, oldValue) {
      if (newValue !== oldValue) {
        this.asyncFind();
        this.value = [];
        this.optionArray = [];
        this.optionsRaw = {};
      }
    }
  },
  mounted: function() {
    if (this.search && this.taxonomy != "") {
      this.asyncFind();
    }
    this.optionArray = JSON.parse(this.options) || [];
  },
  methods: {
    onChange(values) {
      this.value = values;
    },
    customLabel(value) {
      return this.optionsRaw[value];
    },
    remove(removedOption, id) {
      this.value = this.value
        .filter(v => v.value !== removedOption.value)
        .map(v => ({ value: v.value, label: v.label }));
    },
    asyncFind(query = "") {
      this.isLoading = true;
      const config = {
        taxonomy: this.taxonomy,
        search: query
      };
      ajaxCall(
        this.$store.state.ajaxurl,
        this.search,
        this.$store.state.nonce,
        "data",
        JSON.stringify(config)
      ).then(res => {
        if (res.ok) {
          const obj = this;
          res.json().then(value => {
            if (!Array.isArray(value.data)) {
                value.data = Object.values(value.data);
            }
            value.data.forEach(element => {
              obj.optionsRaw[element.term_id] = element.name;
              obj.optionArray.push(element.term_id);
            });
            this.isLoading = false;
          });
        } else {
          this.optionsRaw = {};
          this.optionArray = [];
          this.isLoading = false;
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.multiple-select-wrapper {
  display: flex;
  align-items: center;
  span {
    margin-right: 8px;
  }
  margin-bottom: 8px;
}
</style>
